.form {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    position: absolute;
    bottom: 45vh;
    width: 100vw;
    height: 4rem;
    margin: 0px;
    z-index: 9999999;
}

.form_input {
    font-size: 1rem;
    margin-right: 1rem;
    margin-left: 1rem;
    border-radius: .4vw;
    border-width: 0px;
    outline: none;
    padding: 1vw;
    width: 30vw;
    z-index: 99999999;
    background: rgba(82, 6, 82, 0.3);
    color: white;
}

.form_button {
    background: rgba(82, 8, 82, 0.8);
    color: white;
    cursor: pointer;
    font-size: 1rem;
    padding: .7rem;
    border-radius: .4rem;
    border-width: 0px;
    outline: none;
    width: 5rem;
    z-index: 9999999;
    color: white;
}
